import { Platform } from '@ionic/angular';
import { StatusItemInspecionadoService } from './../../../service/sigobra/status-item-inspecionado.service';
import { StatusitensinspecaoService } from './../../../service/sigobra/statusitensinspecao.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { Inspecao } from './../../../model/objetc/inspecao';
import { InspecaoService } from './../../../service/sigobra/inspecao.service';
import { ObraService } from './../../../service/sigobra/obra.service';
import { Obras } from './../../../model/objetc/obras';
import { TipoinspecaoService } from './../../../service/sigobra/tipoinspecao.service';
import { TipoInspecao } from './../../../model/objetc/tipoinspecao';
import { InspecaoItemTipoInspecao } from '../../../model/objetc/inspecaoitemtipoinspecao';
import { UsuarioService } from './../../../service/sigobra/usuario.service';
import { Usuario } from '../../../model/objetc/usuario';
import { TipoStatus } from '../../../model/enums/tipo-status.enum';
import { StatusItemInspecao } from '../../../model/objetc/statusiteminspecao';
import { StatusItemInspecionado } from '../../../model/objetc/status-item-inspecionado';
import { ItemInspesionadoService } from '../../../service/sigobra/item-inspesionado.service';

@Component({
  selector: 'app-inspecao-realizar',
  templateUrl: './inspecao-realizar.page.html',
  styleUrls: ['./inspecao-realizar.page.scss'],
})
export class InspecaoRealizarPage implements OnInit {


  inspecao: Inspecao = new Inspecao();
  obras: Array<Obras>;
  tipoinspecoes: Array<TipoInspecao>;
  inspecaoitens: Array<InspecaoItemTipoInspecao>;
  inspecaoitem: InspecaoItemTipoInspecao;
  usuario: Usuario = new Usuario;

  statusItem: Array<StatusItemInspecao>

  statusBotao = TipoStatus;


  constructor(
    private base: Base, private us: UsuarioService, private is: InspecaoService, private tis: TipoinspecaoService,
              private os: ObraService,
              private siis: StatusitensinspecaoService,
              private statusServe: StatusItemInspecionadoService,
              private iis: ItemInspesionadoService,
              private router: Router) {
  }

  ngOnInit() {
    if ((this.is.inspecao !== undefined) && (this.is.inspecao !== null)) {
      this.inspecao = this.is.inspecao;
    }
    this.carregarinspecaoitens();
    this.getuser();
    this.carregarStatus();
  }

  carregarinspecaoitens() {
    /*  if (this.inspecao.inspecaoItemTipoInspecao !== undefined &&
        this.inspecao.inspecaoItemTipoInspecao !== null &&
        this.inspecao.inspecaoItemTipoInspecao.length > 0 ) {
          this.inspecaoitens = this.inspecao.inspecaoItemTipoInspecao;
          return;
      } */
    this.base.present();
    this.is.byinspecaoitens(this.inspecao).subscribe(
      data => {
        this.base.dismiss();
        this.inspecaoitens = data as Array<InspecaoItemTipoInspecao>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao cosultar  Inspecao Itens' + this.base.tratarErro(error));
      }
    );
  }

  carregarStatus() {
    return this.siis.pesquisar(' ').subscribe(
      data => {
        this.statusItem = data as Array<StatusItemInspecao>;
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  status ' + this.base.tratarErro(error));
      }
    );
  }
  get editando() {
    return Boolean(this.inspecao.id)
  }

  salvar() {
    this.salvando();
  }

  salvando() {
    this.base.present();
    this.is.salvar(this.inspecao).subscribe(
      data => {
        this.is.inspecao = new Inspecao();
        this.base.dismiss();
        this.router.navigate(['inspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao salvar inspecao :' + this.base.tratarErro(error));
      }
    );
  }

  getClass(nome: InspecaoItemTipoInspecao) {
    return true;
  }

  getuser() {
    return this.us.as.token.usuario;
  }

  inicial(inspecaoitem: InspecaoItemTipoInspecao) {

    return (inspecaoitem.statusInspecao === null || inspecaoitem.statusInspecao === undefined) ||
      (inspecaoitem.statusInspecao.statusiteminspecao === null || inspecaoitem.statusInspecao.statusiteminspecao === undefined) ||
      (inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus === null ||
        inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus === undefined) ||
      TipoStatus[inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus.toString()] === TipoStatus.aberto;
  }

  aberto(inspecaoitem: InspecaoItemTipoInspecao) {

    return (inspecaoitem.statusInspecao === null || inspecaoitem.statusInspecao === undefined) ||
      (inspecaoitem.statusInspecao.statusiteminspecao === null || inspecaoitem.statusInspecao.statusiteminspecao === undefined) ||
      (inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus === null ||
        inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus === undefined) ||
      TipoStatus[inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus.toString()] === TipoStatus.aberto ||
      TipoStatus[inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus.toString()] === TipoStatus.inspecionadaReprovado;
  }

  inspecionado(inspecaoitem: InspecaoItemTipoInspecao) {
    try {
      return (TipoStatus[inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus.toString()]
        === TipoStatus.inspecionadaAprovado ||
        TipoStatus[inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus.toString()]
        === TipoStatus.reinspecionadaAprovado );
    } catch (error) {
      return false;
    }
  }

  reprovadoAprovado(inspecaoitem: InspecaoItemTipoInspecao) {

    try {
      return (TipoStatus[inspecaoitem.statusInspecao.statusiteminspecao.tipoStatus.toString()]
        === TipoStatus.inspecionadaReprovado);
    } catch (error) {
      return false;
    }
  }

  inspencionar(itemInspencao: InspecaoItemTipoInspecao) {
    /* if ((itemInspencao.statusInspecao.statusiteminspecao.tipoStatus in [TipoStatus.aberto, TipoStatus.finalizado, 'aberto', 'finalizado'])) {
       this.base.mensagemErro('Selecione um status de inspenção que tenha com tipo aberto ou finalizado');
       return;
     } */

    if (itemInspencao.statusInspecao === null || itemInspencao.statusInspecao === undefined) {
      itemInspencao.statusInspecao = new StatusItemInspecionado();
    }
    itemInspencao.statusInspecao.pessoa = this.statusServe.as.token.usuario.pessoa;
    itemInspencao.statusInspecao.statusiteminspecao = this.buscarStatusPorTipo(TipoStatus.inspecionadaAprovado);
    this.base.present();
    this.statusServe.salvar(itemInspencao.statusInspecao, itemInspencao.id).subscribe(
      data => {
        itemInspencao.statusInspecao = (data as StatusItemInspecionado);

        this.base.dismiss();
        this.base.menssagemSucesso('Inspenção registrada com sucesso');
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Erro ao salvar inspenção :' + this.base.tratarErro(error));
      }
    );
  }

  inspencionarReprovar(itemInspencao: InspecaoItemTipoInspecao) {
    /*if (!(itemInspencao.statusInspecao.statusiteminspecao.tipoStatus === TipoStatus.reprovado ||
      itemInspencao.statusInspecao.statusiteminspecao.tipoStatus.toString() === 'reprovado')) {
      this.base.mensagemErro('Selecione um status de inspenção que tenha como tipo reprovado');
      return;
    } */
    if (itemInspencao.statusInspecao === null || itemInspencao.statusInspecao === undefined) {
      itemInspencao.statusInspecao = new StatusItemInspecionado();
    }
    itemInspencao.statusInspecao.pessoa = this.statusServe.as.token.usuario.pessoa;
    itemInspencao.statusInspecao.statusiteminspecao = this.buscarStatusPorTipo(TipoStatus.inspecionadaReprovado);
    this.base.present();
    this.statusServe.salvar(itemInspencao.statusInspecao, itemInspencao.id).subscribe(
      data => {
        itemInspencao.statusInspecao = (data as StatusItemInspecionado);
        this.base.dismiss();
        this.base.menssagemSucesso('Inspenção registrada com sucesso');
        //itemInspencao.statusInspecao.push(itemInspencao.novoStatus);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Erro ao salvar inspenção :' + this.base.tratarErro(error));
      }
    );
  }
  ReinspencionarAprovar(itemInspencao: InspecaoItemTipoInspecao) {
    /* if ((itemInspencao.statusInspecao.statusiteminspecao.tipoStatus in [TipoStatus.finalizado, 'finalizado'])) {
       this.base.mensagemErro('Selecione um status de inspenção que tenha com tipo finalizado');
       return;
     } */
    if (itemInspencao.statusInspecao === null || itemInspencao.statusInspecao === undefined) {
      itemInspencao.statusInspecao = new StatusItemInspecionado();
    }
    itemInspencao.statusInspecao.pessoa = this.statusServe.as.token.usuario.pessoa;
    itemInspencao.statusInspecao.statusiteminspecao = this.buscarStatusPorTipo(TipoStatus.reinspecionadaAprovado);
    this.base.present();
    this.statusServe.salvar(itemInspencao.statusInspecao, itemInspencao.id).subscribe(
      data => {
        itemInspencao.statusInspecao = (data as StatusItemInspecionado);
        this.base.dismiss();
        this.base.menssagemSucesso('Inspenção registrada com sucesso');
        // itemInspencao.statusInspecao.push(itemInspencao.novoStatus);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Erro ao salvar inspenção :' + this.base.tratarErro(error));
      }
    );
  }

  reabrir(itemInspencao: InspecaoItemTipoInspecao) {
    /* if ((itemInspencao.statusInspecao.statusiteminspecao.tipoStatus in [TipoStatus.finalizado, 'finalizado'])) {
       this.base.mensagemErro('Selecione um status de inspenção que tenha com tipo finalizado');
       return;
     } */
    if (itemInspencao.statusInspecao === null || itemInspencao.statusInspecao === undefined) {
      itemInspencao.statusInspecao = new StatusItemInspecionado();
    }
    itemInspencao.statusInspecao.pessoa = this.statusServe.as.token.usuario.pessoa;
    itemInspencao.statusInspecao.statusiteminspecao = this.buscarStatusPorTipo(TipoStatus.aberto);
    this.base.present();
    this.statusServe.reabrir(itemInspencao.statusInspecao, itemInspencao.id).subscribe(
      data => {
        itemInspencao.statusInspecao = (data as StatusItemInspecionado);
        this.base.dismiss();
        this.base.menssagemSucesso('Inspenção reaberta com sucesso');
        // itemInspencao.statusInspecao.push(itemInspencao.novoStatus);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Erro ao reabrir inspenção :' + this.base.tratarErro(error));
      }
    );
  }

  getColor(inspecaoitem: InspecaoItemTipoInspecao) {
    try {
      if (inspecaoitem.statusInspecao !== null && inspecaoitem.statusInspecao !== undefined &&
        inspecaoitem.statusInspecao.statusiteminspecao !== null && inspecaoitem.statusInspecao.statusiteminspecao !== undefined &&
        inspecaoitem.statusInspecao.statusiteminspecao.cor !== undefined && inspecaoitem.statusInspecao.statusiteminspecao.cor !== null) {
        return this.getColorStatus(inspecaoitem.statusInspecao.statusiteminspecao);
      } else {
        return this.getColorByStatus(TipoStatus.aberto);
      }
    } catch (error) {
      return 'transparent';
    }
  }


  getColorStatus(status: StatusItemInspecao) {
    return status.cor;
  }

  getColorByStatus(tipostatus: TipoStatus) {
    let cor: string = '';

    this.statusItem.forEach(element => {
      if (TipoStatus[element.tipoStatus.toString()] === tipostatus) {
        cor = element.cor;
      }
    });
    if (cor !== '') {
      return cor;
    } else {
      return 'transparent';
    }
  }

  buscarStatusPorTipo(tipostatus: TipoStatus) {
    let statu: StatusItemInspecao = this.statusItem[0];
    this.statusItem.forEach(element => {
      if (TipoStatus[element.tipoStatus.toString()] === tipostatus) {
        return statu = element;
      }
    });

    return statu;
  }

  msgExpandida(inspecaoitem: InspecaoItemTipoInspecao) {
    this.base.mensagemAviso(inspecaoitem.statusInspecao.descricaoProblema, 'Descrição');
  }

  msgExpmv(inspecaoitem: InspecaoItemTipoInspecao) {
    this.base.mensagemAviso(inspecaoitem.itemtipoinspecao.metodoverificacao, 'Método de Verificação');
  }

  temMsg(inspecaoitem: InspecaoItemTipoInspecao) {
    try {
      return inspecaoitem.statusInspecao.descricaoProblema !== undefined &&
      inspecaoitem.statusInspecao.descricaoProblema !== null &&
      inspecaoitem.statusInspecao.descricaoProblema !== '';
    } catch (error) {
      return false;
    }
  }

  selecionarobra(event: any) { }

  selecionartipoinspecao(event: any) { }

  selecionarStatus(event: any) {
    // itemInspencao.statusInspecao.statusiteminspecao = null;
  }
}
